<template>
  <v-navigation-drawer
    v-model="show"
    app
    temporary
  >
    <v-subheader>订阅列表</v-subheader>
    <v-list-item
      v-for="(li, i) of currFeeds"
      :key="i"
      @click="show=false;$store.dispatch('loadNews', li.feedUrl)"
    >
      <v-list-item-content>
        <v-list-item-title v-text="li.title"></v-list-item-title>
      </v-list-item-content>
      <!-- <v-list-item-action>
        <v-icon>mdi-delete-outline</v-icon>
      </v-list-item-action> -->
    </v-list-item>
    <v-subheader>工具</v-subheader>
    <v-list-item @click="importFile">
      <v-list-item-icon>
        <v-icon>mdi-file-import</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>导入</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-item @click="exportFile">
      <v-list-item-icon>
        <v-icon>mdi-file-export</v-icon>
      </v-list-item-icon>
      <v-list-item-content>
        <v-list-item-title>导出</v-list-item-title>
      </v-list-item-content>
    </v-list-item>
    <v-list-item @touchstart="godMode"></v-list-item>
    <input style="display:none" @change="selectedFile" type="file" ref="file" accept="text/xml" />
  </v-navigation-drawer>
</template>

<script>
import parseOpml from 'node-opml-parser'
import opml from 'opml-generator'
import { ref, watch } from '@vue/composition-api'
export default {
  model: {
    event: 'toggle',
    prop: 'visible',
  },
  props: {
    visible: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, ctx) {
    const show = ref(false)
    watch(() => props.visible, v => show.value = v)
    watch(show, v => ctx.emit('toggle', v))

    const defaultFeeds = [
      { title: '阮一峰的博客', feedUrl: 'http://www.ruanyifeng.com/blog/atom.xml' },
      { title: 'IT之家', feedUrl: 'https://www.ithome.com/rss/' },
      { title: '前端艺术家', feedUrl: 'https://rsshub.app/jskou/0' },
      { title: '飞冰早报', feedUrl: 'https://rsshub.app/jskou/1' },
    ]

    const feeds = localStorage.getItem('feed') ? JSON.parse(localStorage.getItem('feed')) : defaultFeeds
    const currFeeds = ref(feeds)

    // 更新浏览器缓存feed
    const updateFeeds = () => {
      localStorage.setItem('feed', JSON.stringify(currFeeds.value))
    }

    const importFile = () => {
      ctx.refs.file.click()
    }
    // 处理文件选择
    const selectedFile = ({ target }) => {
      const file = target.files[0]
      const reader = new FileReader()
      reader.onload = evt => {
        parseOpml(evt.target.result, (err, feeds) => {
          if (currFeeds.value.length > 0) {
            // 过滤本地已有feed
            feeds = feeds.filter(feed => !currFeeds.value.find(my => my.feedUrl === feed.feedUrl))
          }
          currFeeds.value = currFeeds.value.concat(feeds)
          updateFeeds()
        })
      }
      reader.readAsText(file)
    }

    const exportFile = async () => {
      const title = window.prompt('Title') || 'AkiReader'
      const header = {
        title,
        dateCreated: new Date(),
        ownerName: 'AkiReader',
      }
      // const index = this.myFeeds.findIndex(feed => feed.title === this.hide.title)
      // const data = [].concat(this.myFeeds)
      // if (index > -1) data.splice(index, 1)
      const outlines = currFeeds.value.map(feed => ({
        text: feed.title,
        title: feed.title,
        xmlUrl: feed.feedUrl,
        htmlUrl: feed.url || '',
        type: feed.feedType || 'rss',
      }))
      const txt = opml(header, outlines)
      const url = URL.createObjectURL(new Blob([txt], { type: 'text/xml' }))
      const a = document.createElement('a')
      a.href = url
      a.download = 'opml'
      a.style.display = 'none'
      document.body.appendChild(a)
      a.click()
    }
    const count = ref(0)
    let timer = null
    const cl = [
      { title: '无壳海兔', feedUrl: 'https://rsshub.app/t66y/2/2' },
      { title: '大嘴鸥', feedUrl: 'https://rsshub.app/t66y/4/2' },
      { title: '美丽花', feedUrl: 'https://rsshub.app/t66y/5/2' },
      { title: '高傲雉鸡', feedUrl: 'https://rsshub.app/t66y/7/2' },
      { title: '新时代', feedUrl: 'https://rsshub.app/t66y/8/2' },
      { title: '达盖尔', feedUrl: 'https://rsshub.app/t66y/16/2' },
      { title: '文人文言', feedUrl: 'https://rsshub.app/t66y/20/2' },
    ]
    const godMode = () => {
      if (count.value > 7) {
        const str = window.prompt()
        if (str == 2 ** 10) currFeeds.value = cl
      }
      count.value++
      clearTimeout(timer)
      timer = setTimeout(() => {
        count.value = 0
      }, 2000)
    }
    godMode()
    return {
      show,
      currFeeds,
      selectedFile,
      importFile,
      exportFile,
      godMode,
    }
  },
}
</script>