<template>
  <v-app id="scroll-target">
    <v-app-bar
      app
      color="primary"
      light
    >
      <v-app-bar-nav-icon v-if="$route.name === 'home'" @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-btn v-else text icon @click="$router.back()">
        <v-icon>mdi-arrow-left</v-icon>
      </v-btn>

      <v-toolbar-title>AkiReader</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-btn v-if="$route.name === 'item'" text icon @click="linkTo($route.params.link)">
        <v-icon>mdi-link</v-icon>
      </v-btn>
      <v-btn v-else text icon @click="$store.dispatch('loadNews')">
        <v-icon>mdi-refresh</v-icon>
      </v-btn>

    </v-app-bar>

    <Menu v-model="drawer" />
    <v-content v-scroll="onScroll">
      <router-view @close="drawer=false" />
    </v-content>
    <v-fab-transition>
      <v-btn
        v-show="scrollTop > 520"
        fixed
        dark
        fab
        bottom
        right
        color="primary"
        @click="$vuetify.goTo(0)"
      >
        <v-icon>mdi-chevron-up</v-icon>
      </v-btn>
    </v-fab-transition>
    <v-snackbar
      v-model="$store.state.snackbar"
    >
      {{ $store.state.error }}
    </v-snackbar>
  </v-app>
</template>

<script>
import Menu from './components/Menu'
import { ref } from '@vue/composition-api'

export default {
  name: 'App',

  components: {
    Menu,
  },

  setup(_, ctx) {
    const drawer = ref(false)

    const linkTo = link => window.open(link)

    const scrollTop = ref(0)

    const onScroll = e => {
      scrollTop.value = e.target.scrollingElement.scrollTop
    }
    ctx.root.$store.dispatch('loadNews')
    return {
      drawer,
      linkTo,
      scrollTop,
      onScroll,
    }
  },
}
</script>

