import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const CORS_PROXY = 'https://cors-anywhere.herokuapp.com/'
import P from 'rss-parser'
const paser = new P()
export default new Vuex.Store({
  state: {
    news: {},
    loading: true,
    snackbar: false,
    error: '',
    url: 'http://www.ruanyifeng.com/blog/atom.xml',
  },
  mutations: {
    update(state, data) {
      state.news = data
    },
    hideLoading(state) {
      state.loading = false
    },
    showLoading(state) {
      state.loading = true
    },
    showSnackbar(state, txt) {
      state.snackbar = true
      state.error = txt
    },
    saveUrl(state, url) {
      state.url = url
    },
  },
  actions: {
    async loadNews({ commit, state }, url) {
      const URL = url || state.url
      commit('saveUrl', URL)
      commit('showLoading')
      try {
        const data = await paser.parseURL(CORS_PROXY + URL)
        commit('update', data)
      } catch (error) {
        commit('showSnackbar', error)
      }
      commit('hideLoading')
    },
  },
  modules: {},
})
