import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Main,
  },
  {
    path: '/item',
    name: 'item',
    component: () => import('../views/Item.vue'),
  },
]

const router = new VueRouter({
  routes,
})

export default router
